@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@100;200;300;400;500;600;700;800;900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

:root {
  --title-font: 'Montserrat Alternates', sans-serif;
  --text-color: black;
  --light-accent-color: #00E9AA;
  --dark-accent-color: #84FFC5;
  --accent-color: var(--dark-accent-color);

  --light-bg: white;
  --dark-bg: rgb(14, 14, 15);
  --bg: var(--light-bg);
  --cursor-color: black;
}

body {
  background: white;
  background: var(--bg);
}

li {
  cursor: pointer;
}

h1 {
  font-family: 'Montserrat Alternates', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-family: var(--title-font), -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 800;
  line-height: 3rem;
}

p {
  line-height: 1.6rem;
}

a {
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
}

button {
  border: none;
  padding: 12px 18px;
  background-color: #84FFC5;
  background-color: var(--accent-color);
  font-size: .9rem;
  color: black;
}


.page404 {
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.page404>img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.page404>h1 {
  z-index: 100;
  text-align: center;
  font-size: 4rem;
  padding-bottom: 10px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.home {
  height: 100vh;
  width: 100%;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 200px auto 200px;
      grid-template-columns: 200px auto 200px;
}

.nav {
  border-right: 2px solid whitesmoke;
}

.nav img {
  width: 100%;
}

.nav header {
  padding: 20px;
}

.nav header img {
  width: 80px;
}

.nav-links {
  height: 100%;
}

.nav-links img {
  width: 20px;
  margin-right: 10px;
}

.nav-links a {
  width: 100%;
  height: 100%;
  padding: 10px;
  color: var(--text-color);
  display: flex;
  align-items: center;
}

.nav-links .nav-link {
  margin: 10px;
  cursor: pointer;
  transition: transform 100ms ease, background 200ms ease;
}

.nav-links .nav-link:hover {
  transform: scale(1.05);
  font-weight: 600;
}

.nav-links .selected-nav-link {
  background: var(--accent-color);
  font-weight: 600;
}
